@import './variables';

h1, h2 {
  margin: 0;
  font-size: 50px;
  line-height: 70px;
  letter-spacing: 1px;
  font-weight: 100;
}

h2 {
  color: #ccc;
}

body {
  font-family: $font-family-body;
}

a {
  text-transform: uppercase;
  font-family: $font-family-btn;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 600;
  color: black;
}
